							transform: translateX(-50%);
							margin-top: 3vmin;
						}
						span {
							font: 12px/1.35 'Poppins';
							color: rgb(105, 13, 13);
							margin-top: 3px;
						}
						.wrap {
							width: 100%;
							padding: 15vmin 10vmin 0;
							textarea {
								width: 100%;
								height: 45vmin;
								padding: 2vmin;
								resize: none;
								font: normal 14px/1.5 'Poppins';
								margin-bottom: 3vmin;
							}
							.agreement {
								text-align: center;
								label[for='agreement'] {
									font: 14px/1 'Poppins';
									input[type='checkbox'] {
										margin-right: 1vmin;
									}
								}
							} //input[[type='radio']   input   .   []  .
						}
						table {
							width: 100%;
							margin: 2vmin 2vmin 5vmin;
							padding: 0;
							text-align: left;
							border-collapse: collapse;
							font: 14px/1 'Poppins';
							tr {
								width: 100%;
								border-bottom: 1px solid #ddd;
								&:nth-of-type(1) {
									border-top: 1px solid #ddd;
								}
								th {
									width: 30%;
									padding-left: 2vmin;
									font: bold 14px/1 'Poppins';
								}
								td {
									padding: 10px 0 10px 5px;
									#id,
									#pwd1,
									#pwd2 {
										width: 95%;
										height: 22px;
										font: 14px/1 'Poppins';
									}
									#name {
										width: 50%;
									}
									#birth {
										width: 70%;
										font: 14px/1 'Poppins';
									}
									#address {
										width: 70%;
										font: 14px/1 'Poppins';
									}
									#address2 {
										width: 95%;
										font: 14px/1 'Poppins';
									}
									button {
										margin: 0 10px;
										padding: 2px 6px;
										font-size: 12px;
									}
									select {
										font: 14px/1 'Poppins';
										padding: 2px;
									}
									#phone2,
									#phone3 {
										width: 30%;
										font: 14px/1 'Poppins';
									}
									#email {
										width: 95%;
										font: 14px/1 'Poppins';
									}
									label[for='sms1'],
									label[for='sms2'] {
										margin-right: 10px;
									}
								}
							}
							input[type='text'] {
								font: 2vmin 'arial';
								height: 22px;
								padding-left: 1vmin;
								border: none;
							}
							input[type='radio'] {
								font: 14px 'arial';
								margin-right: 0.5vmin;
								&:nth-of-type(2) {
									margin-left: 1.5vmin;
								}
							}
						}
					}

					.wrapper2 {
						h2 {
							position: absolute;
							left: 50%;
							font: bold 5vmin/1 'Poppins';
							top: 5vmin;
							transform: translateX(-50%);
						}
						table {
							width: 100%;
							margin: 15vmin 2vmin 80px;
							border-collapse: collapse;
							font: 14px 'Poppins';

							tr {
								border-bottom: 1px solid #ddd;
								&:nth-of-type(1) {
									border-top: 1px solid #ddd;
									border-bottom: none;
									td {
										padding-top: 10px;
									}
								}
								&:last-child {
									border-bottom: none;
								}
								th {
									width: 30%;
									padding-left: 2vmin;
								}
								td {
									padding: 10px 0px;
								}
								button {
									margin: 0 3px;
									padding: 5px;
									font: 12px/1 'arial';
								}
								select {
									font: 14px/1 'Poppins';
									margin: 0 5px;
								}
								input[type='text'] {
									width: 50%;
									margin: 0 5px;
									font: 14px/1 'arial';
									height: 22px;
									border: none;
								}
								input[type='radio'] {
									font: 14px 'arial';
									margin-left: 10px;
								}
								& #submit {
									position: absolute;
									bottom: 24px;
									font: 14px/1 'arial';
									background: #a7a39e;
									right: 50%;
									color: white;
									margin: 0 10px;
									border: none;
									padding: 10px;
								}
								& #reset {
									position: absolute;
									bottom: 24px;
									font: 14px/1 'arial';
									background: #a7a39e;
									left: 50%;
									color: white;
									margin: 0 10px;
									border: none;
									padding: 10px;
								}
							}
						}
						.pic {
							display: none;
						}
					}
				}
			}
		}
	}
}
