.board {
	width: 100%;

	.inner {
		margin: 15vmin auto 15vmin;
		position: relative;

		.list {
			h1 {
				font: bold 8vmin/1 'Lora';
				margin: 5vmin 0 3vmin 15vmin;
			}
			span.intro {
				width: 80%;
				display: inline-block;
				font: 2.5vmin/1.5 'Poppins';
				margin: 3vmin 0 10vmin 15vmin;
			}
			.box {
				width: 80%;
				font: 2.5vmin/1 'arial';
				border-top: 1px solid #ddd;
				margin: 0 auto;
				padding: 2vmin;
				position: relative;
				display: flex;
				&:last-of-type {
					border-bottom: 1px solid #ddd;
				}
				span {
					margin-left: 10vmin;
				}
				h2 {
					display: inline-block;
					margin: 0 2vmin;
					font: bold 2.5vmin/1 'Poppins';
				}
			}
			.hidden_box {
				width: 80%;
				margin: 0 auto;
				margin: 1vmin 0 10vmin 15vmin;
				position: relative;
				> p {
					font: 2vmin/1 'Poppins';
					width: 80%;
					padding: 1vmin 3vmin;
					margin: 0 auto;
				}
			}
			.writeBox {
				width: 80%;
				margin: 10vmin auto;
				border: 1px solid #aaa;
				padding: 5vmin 0;
				position: relative;
				> .title {
					width: 80%;
					margin: 2vmin auto;
					label {
						display: inline-block;
						padding-right: 10px;
						font: bold 2vmin/1 'Poppins';
					}
					input[type='text'] {
						width: 85%;
						margin: 0 auto;
						display: inline-block;
						font: 2vmin/1 'Poppins';
					}
				}
				> .text {
					width: 80%;
					margin: 2vmin auto;
					label {
						display: inline-block;
						padding-right: 10px;
						vertical-align: top;
						font: bold 2vmin/1 'Poppins';
					}
					textarea {
						width: 85%;
						height: 10vmin;
						margin: 0 auto;
						display: inline-block;
						font: 2vmin/1 'Poppins';
					}
				}
			}

			button {
				position: absolute;
				bottom: -8vmin;
				right: 15vmin;
				padding: 2px 10px;
				border: 2px solid #222;
				font: bold 2vmin/1 'Poppins';
				background: #fff;
			}

			.buttonWrap {
				position: relative;
				text-align: right;
				width: 90%;
				margin: 0 auto;
				> button {
					position: relative;
					bottom: 0;
					right: 0;
					margin-right: 15px;
					padding: 2px 10px;
					border: 2px solid #222;
					font: bold 2vmin/1 'Poppins';
					background: #fff;
				}
			}
		}
	}
}

